import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './TitleFooter.css';

export default class TitleFooter extends Component{
    render(){
        return (
            <footer className="footer">
                <div>
                    <div>
                        <p>Powered by <a className="link" href="https://coinpaprika.com/">coinpaprika.com</a></p>
                    </div>
                    <div>
                        <p>Designed by: <em>Denis Da Cruz - 2022</em></p>
                    </div>
                    <div>
                        <a href="https://github.com/Cyberdjent76/coin-exchange" 
                            rel="noopener noreferrer" target="_blank">
                            <FontAwesomeIcon icon="fa-brands fa-github" size='2x' spin spin-reverse />
                        </a>
                    </div>
                </div>
            </footer>
        );
    }
}
